  }
}
@media (max-width: 900px) {
  .header__navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    transition: 0.4s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #141617;
    border-radius: 8px;
    padding: 24px 0;
