  }
  .header__navbar.active {
    display: flex;
    animation: swing-in-top-fwd 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  }
  .header__navbar.in-active {
    display: flex;
    animation: slide-out-bck-top 0.5s cubic-bezier(0.6, -0.28, 0.735, 0.045) both;
  }
