  margin-left: 8px;
}
.header__icons #menu-btn {
  display: none;
}
@media (max-width: 900px) {
  .header__icons #menu-btn {
    display: inline-block;
  }
}
@media (min-width: 901px) {
  .header {
    padding: 8px, 16px;
  }
}

.contact {
