.home {
  padding: 0;
  color: #fff;
}
.home__slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.home__slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, rgba(20, 22, 23, 0.75) 0%, rgba(20, 22, 23, 0) 100%);
