    font-size: 32px;
    text-transform: capitalize;
  }
}
.home__content p {
  font-size: 24px;
  line-height: 2;
  line-height: 32px;
  padding: 24px 0;
}
.home__btn {
  transition: all 0.2s linear;
  font-size: 40px;
  padding: 16px;
  color: #f2662c;
  opacity: 0.7;
}
@media (max-width: 900px) {
  .home__btn {
    display: none;
  }
}
.home__btn:hover {
  opacity: 1;
  transform: scale(1.2);
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.pricing {
  color: #fff;
}
.pricing .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 2.4rem;
}
.pricing .box-container .box {
  background-color: #141617;
  color: #fff;
  text-align: center;
  padding: 2rem;
  border: 0.1rem solid rgba(0, 0, 0, 0.3);
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
  border-radius: 0.9rem;
}
.pricing .box-container .box i {
  font-size: 40px;
  color: #fff;
  height: 7rem;
  line-height: 7rem;
  margin-bottom: 1rem;
  background: #f2662c;
  border-radius: 50%;
  width: 7rem;
  box-shadow: hsla(0deg, 0%, 0%, 0.14), 0px 5px 22px 4px hsla(0deg, 0%, 0%, 0.12), 0px 7px 8px -4px hsla(0deg, 0%, 0%, 0.2);
}
@media (max-width: 900px) {
  .pricing .box-container .box i {
    font-size: 32px;
  }
}
.pricing .box-container .box h3 {
  font-family: "Bruno Ace";
  font-size: 32px;
  text-transform: capitalize;
  font-weight: 400;
  padding: 2rem 0;
  color: #fff;
}
.pricing .box-container .box .price {
  padding: 1rem 0;
  font-family: "Bruno Ace";
  font-size: 48px;
  text-transform: capitalize;
  color: #fff;
}
.pricing .box-container .box .price span {
  font-size: 24px;
  color: #f2662c;
}
