.reviews__slide p {
  padding: 16px;
  background-color: #fff;
  color: #141617;
  position: relative;
  margin-top: 24px;
  line-height: 16px;
  text-align: center;
  font-size: 24px;
  line-height: 2;
}
.reviews__slide p::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 48px;
  height: 32px;
  width: 32px;
  background-color: #fff;
  box-shadow: 0px 1px 10px 0px hsla(0deg, 0%, 0%, 0.12), 0px 2px 4px -1px hsla(0deg, 0%, 0%, 0.2);
  transform: rotate(45deg);
  z-index: -1;
}
