.contact {
	display: flex;
	align-items: center;
	flex-direction: column;
	&__subtitle {
		@include graphText(24px);
		color: $white;
		text-align: center;
	}

	&__column {
		float: left;
		width: 50%;
		margin-top: 6px;
		padding: 20px;
		@include for-tablet-landscape-down {
			width: 100%;
			margin-top: 0;
		}

		iframe {
			margin-top: 40px;
			width: 100%;
			height: 500px;
		}
		label {
			color: $white;
			@include graphText(24px);

			@include for-tablet-landscape-down {
				align-self: flex-start;
				@include graphText(16px);
			}
		}
		input[type="text"],
		select,
		textarea {
			width: 100%;
			padding: 8px;
			margin-top: 8px;
			margin-bottom: 4px;
			resize: vertical;
			@include graphText(16px);
			color: $gray-800;
			background: $dark-gray;
			border-bottom: $border;
			border-color: rgba($color: $primary, $alpha: 0.6);

			&:focus {
				border-color: rgba($color: $primary, $alpha: 1);
				color: $white;
			}
		}
	}
	&__row {
		background-color: $dark-gray;
		color: $white;
		margin-top: 32px;
		padding: 16px;
		border: $border;
		box-shadow: $box-shadow-intense;
		border-radius: $border-radius__small;
		content: "";
		display: table;
		clear: both;
	}
}
